import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            items: [],
            act: 0,
            children: [],
            search: '',
            //user: this.$store.state.userInfo.Data,
            inputFlag: false,
            check:[],
            nowCheck:[],
            nowTarget: null,
            Patient: this.$store.state.patient,
            user: this.$store.state.userInfo,
            isImEntry:this.$route.query.isImEntry,
        }
    },
    created(){
        if(this.$store.state.SaveDiease){
            this.check = this.$store.state.SaveDiease
        }else{
           this.$get('PlatFormAPI/DiseaseGroupAndRule/QueryPatientDisGroup',{
                PatientId:this.Patient.PatientId
            }).then(o => {
                this.check = o || []
            }) 
        }
        if(this.$route.query.fromSet){
        	this.$get('PlatFormAPI/DoctorReferral/QueryDoctorDisGroup',{
                DrId:this.user.Data.User.DoctorId
            }).then(o => {
                this.check = o || []
            })
        }
        this.pull(0)
        this.pullOld()
    },
    mounted(){
        if(this.$store.state.serviceBase && this.$store.state.serviceBase.DisGroupIdList){
            this.check = this.$store.state.serviceBase.DisGroupIdList
        }
    },
    methods:{
        choose(id,idx){
            this.act = idx 
            this.nowTarget = id
            this.pull(id)
        },
        pull(ParentId){
            if(!ParentId){
                this.$get('PlatFormAPI/DiseaseGroupAndRule/QueryDiseaseGroupType').then(o => {
                    this.items = o
                    this.nowTarget = o[0].DisGroupTypeId
                    this.pull(o[0].DisGroupTypeId)
                })
            }
            else{
                this.$get('PlatFormAPI/DiseaseGroupAndRule/QueryDiseaseGroup',{
                    DisGroupTypeId:ParentId
                }).then(o => {
                    this.check.forEach(v => {
                        o.forEach(a => {
                            if(v.DisGroupId === a.DisGroupId){
                                a.onCheck = 1
                            }
                        })
                    })
                    this.children = o
                })
            }
        },
        pullOld(){
            if(this.$route.query.fromReferralSet){
                this.$get('PlatFormAPI/DoctorReferral/QueryDoctorDisGroup',{
                    DrId: this.user.Data.User.DoctorId,
                }).then(o => {
                    if(o){
                        this.check = o
                        this.nowCheck = o
                    }
                })
            }
        },
        goList(i,id){
        	
            let idx = null
            this.check.forEach((v,d) => {
                if(v.DisGroupId == i.DisGroupId){
                    idx = d
                }
            })
            
            if(idx === null){
                this.check.push(i)
                this.children[id].onCheck = 1
            }
            else{
            	if(this.$route.query.level != 1 && (!this.$route.query.fromPage || this.$route.query.fromPage == "first") ){
            		let a = false
            		this.nowCheck.forEach((v,k) => {
            			if(v == i){
            				this.$toast("温馨提示：您暂时无法删除已设定好的标签")
            				a= true
            				return
            			}
            			
            		})
            		console.log(a)
            		if(a){
            			
            		}else{
            			this.check.splice(idx,1)
                		this.children[id].onCheck = 0 
            		}
            	}else{
            		this.check.splice(idx,1)
                	this.children[id].onCheck = 0 
            	}
                
            }
        },
        save(){
            if(this.$route.query.fromArc){
                let DisGroupList = []
                this.check.forEach(v => {
                    DisGroupList.push({
                        DisGroupId: v.DisGroupId,
                        DisGroupName: v.DisGroupName,
                    })
                })
                this.$post('PlatFormAPI/DiseaseGroupAndRule/SavePatientDisGroup',{
                    PatientId: this.Patient.PatientId,
                    PatientName: this.Patient.PatientName,
                    DrId: this.user.Data.User.DoctorId,
                    DrName: this.user.Data.User.DoctorName,
                    DisGroupList
                }).then(o => {
                    this.$toast('保存成功')
                    this.$bridge.callhandler('changePatTap')
                    if(this.isImEntry){
                        this.$bridge.callhandler('saveSucess', '123')
                        this.$bridge.callhandler('BackToIM', '123')
                    }else{
                        this.$router.back()
                    }
                    
                })
            }
            else if(this.$route.query.fromReferral){
                let DisGroupList = []
                this.check.forEach(v => {
                    DisGroupList.push({
                        DisGroupId: v.DisGroupId,
                        DisGroupName: v.DisGroupName,
                    })
                })
                this.$bridge.callhandler('dieasesList',DisGroupList)
            }
            else if(this.$route.query.fromReferralSet){
                let DisGroupList = []
                this.check.forEach(v => {
                    DisGroupList.push({
                        DisGroupId: v.DisGroupId,
                        DisGroupName: v.DisGroupName,
                    })
                })
                this.$post('PlatFormAPI/DoctorReferral/SaveDoctorDisGroup',{
                    DrId: this.user.Data.User.DoctorId,
                    DrName: this.user.Data.User.DoctorName,
                    DisGroupList,
                }).then(o => {
                    this.$toast('保存成功')
                })
            }
            else if(this.$route.query.fromReferralAdd || this.$route.query.fromSet || this.$route.query.fromPage){
                let DisGroupList = []
                this.check.forEach(v => {
                    DisGroupList.push({
                        DisGroupId: v.DisGroupId,
                        DisGroupName: v.DisGroupName,
                    })
                })
                this.$post('PlatFormAPI/DoctorReferral/SaveDoctorDisGroup',{
                    DrId: this.user.Data.User.DoctorId,
                    DrName: this.user.Data.User.DoctorName,
                    DisGroupList,
                }).then(o => {
					this.$toast('保存成功')
					this.$router.back()
//                 	if(this.$route.query.fromSet){
//                 		const data = '123'
//                     	this.$bridge.callhandler('Back', data)
//                 	}else{
//                 		this.$toast('保存成功')
//                     	this.$router.back()
//                 	} 
                })
            }
            else{
                this.$store.dispatch('pushSaveDiease',this.check)
                this.$router.back()
            }
        }
    }
}



